import React, { Component } from 'react';
import './InformationFrame.css';
import Next from 'react-icons/lib/fa/angle-double-right';

class InformationFrame extends Component {
    constructor(props){
        super(props);
        this.state = {
        }
        // this.open = this.open.bind(this);
    }       

    render(){
        return(
            <div className={this.props.nameOfClass}>
                <h2>Nhập các thông tin bệnh nhân</h2>
                <div className="select-wrapper-if">
                    <label>Khả năng tài chính: </label>
                    <select className="select-if" onChange = {this.props.changeFinanceability}>
                        <option value="5" defaultValue >Rất Cao</option>
                        <option value="4">Cao</option>
                        <option value="3">Trung bình</option>
                        <option value="2">Thấp</option>
                        <option value="1">Rất Thấp</option>
                    </select>
                </div>    
                <div className="number-input-if">
                    <label>Nhiệt độ sốt: &nbsp; &nbsp; &nbsp; &nbsp;</label>
                    <input type="number" onChange = {this.props.changeTemperature} defaultValue={37} min={37}/>
                </div> 
                <div className="number-input-if">
                    <label>Tuổi: &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;</label>
                    <input type="number" onChange = {this.props.changeAge} defaultValue={22}/>
                </div> 
                <button onClick={this.props.click} className="select-medicine-btn-if"><Next className="next-if"/>Chọn thuốc</button>
            </div>
        )
    }
}

export default InformationFrame;


