import React, { Component } from 'react';
import './App.css';
import InformationFrame from './component/InformationFrame';
import ResultFrame from './component/ResultFrame';
import Hospital from 'react-icons/lib/fa/hospital-o';
import Config from './config.json';

class App extends Component {
  constructor(props){
    super(props);
    this.state = {
      informationFrame: "frame-if",
      resultFrame: "frame-rs pull-right",
      financeability: "5",
      temperature: 37,
      age: 22,
      C_cooked: [],
    }
    this.click = this.click.bind(this);
    this.changeFinanceability = this.changeFinanceability.bind(this);
    this.changeTemperature = this.changeTemperature.bind(this);
    this.changeAge = this.changeAge.bind(this);
    this.caculatorWeight = this.caculatorWeight.bind(this);
    this.standardized = this.standardized.bind(this);
    this.caculatorC = this.caculatorC.bind(this);
    this.utilC = this.utilC.bind(this);
    this.utilS = this.utilS.bind(this);
  }

  click(){
    let weight = this.caculatorWeight();
    console.log('weight', weight);
    let standard = this.standardized(weight);
    console.log('standardized', standard);
    let C = this.caculatorC(standard);
    let C_cooked = [
      {
        name:'Hapacol',
        value: C[0],
      },
      {
        name:'Aspirin',
        value: C[1],
      },
      {
        name:'Paracetamol',
        value: C[2],
      },
      {
        name:'Efferagan',
        value: C[3],
      }
    ]
    C_cooked.sort((a,b) => a.value < b.value ? true : false);
    console.log('C', C_cooked);
    this.setState({C_cooked});
    this.setState({
      informationFrame: this.state.informationFrame === "frame-if" ? "frame-if pull-left" : "frame-if",
      resultFrame: this.state.resultFrame === "frame-rs" ? "frame-rs pull-right" : "frame-rs",
    })
  }

  changeFinanceability(e){
    this.setState({financeability: e.target.value});  
  }

  changeTemperature(e){
    this.setState({temperature: e.target.value}); 
  }

  changeAge(e){
    this.setState({age: e.target.value}); 
  }

  caculatorWeight(){
    let financeability = [];
    let temperature = [];
    let age = [];
    let final = [];
    // financeability
    switch (this.state.financeability) {
      case "1":
        financeability[0]= 0.8;financeability[1]= 0.1;financeability[2]= 0.1;
        break;
      case "2":
        financeability[0]= 0.6;financeability[1]= 0.2;financeability[2]= 0.2;
        break;
      case "3":
        financeability[0]= 0.4;financeability[1]= 0.3;financeability[2]= 0.3;
        break;
      case "4":
        financeability[0]= 0.2;financeability[1]= 0.4;financeability[2]= 0.4;
        break;
      case "5":
        financeability[0]= 0;financeability[1]= 0.5;financeability[2]= 0.5;
        break;                  
      default:
        break;
    }

    // temperature
    let t = this.state.temperature;
    if(t<= 37.5){
      temperature[0]=0.5; temperature[1]=0.1; temperature[2]=0.4;
    }else if(t<= 38.5){
      temperature[0]=0.4; temperature[1]=0.3; temperature[2]=0.3;
    }else if(t<= 39.5){
      temperature[0]=0.3; temperature[1]=0.5; temperature[2]=0.2;
    }else{
      temperature[0]=0.2; temperature[1]=0.7; temperature[2]=0.1;
    }

    // age
    let a = this.state.age;
    if(a<= 2){
      age[0]=0.1; age[1]=0.1; age[2]=0.8;
    }else if(a<= 9){
      age[0]=0.2; age[1]=0.2; age[2]=0.6;
    }else if(a<= 15){
      age[0]=0.4; age[1]=0.3; age[2]=0.3;
    }else if(a<= 59){
      age[0]=0.5; age[1]=0.4; age[2]=0.1;
    }else {
      age[0]=0.2; age[1]=0.2; age[2]=0.6;
    }

    //final
    for(let i=0; i<3;i++){
      final[i] = (financeability[i]+temperature[i]+age[i])/3;
    }

    return final;
  }

  standardized(weight){
    let config = JSON.parse(JSON.stringify(Config));
    for(let e in config){
      config[e].price*= weight[0];
      config[e].sideEffect*= weight[1];
      config[e].strong*= weight[2];
    }
    return config;
  }

  caculatorC({Hapacol,Aspirin,Paracetamol,Efferagan}){
    let price = {};
    let strong = {};
    let sideEffect = {};
    let Splus = [];
    let Sminus = [];
    let C = [];
    // Caculate max and min of every property
    price.max = Math.min(Hapacol.price, Aspirin.price, Paracetamol.price, Efferagan.price);
    price.min = Math.max(Hapacol.price, Aspirin.price, Paracetamol.price, Efferagan.price);
    strong.max = Math.max(Hapacol.strong, Aspirin.strong, Paracetamol.strong, Efferagan.strong);
    strong.min = Math.min(Hapacol.strong, Aspirin.strong, Paracetamol.strong, Efferagan.strong);
    sideEffect.max = Math.min(Hapacol.sideEffect, Aspirin.sideEffect, Paracetamol.sideEffect, Efferagan.sideEffect);
    sideEffect.min = Math.max(Hapacol.sideEffect, Aspirin.sideEffect, Paracetamol.sideEffect, Efferagan.sideEffect);
    // Caculate S
    Splus[0] = this.utilS(Hapacol, price.max, strong.max, sideEffect.max);
    Splus[1] = this.utilS(Aspirin, price.max, strong.max, sideEffect.max);
    Splus[2] = this.utilS(Paracetamol, price.max, strong.max, sideEffect.max);
    Splus[3] = this.utilS(Efferagan, price.max, strong.max, sideEffect.max);
    Sminus[0] = this.utilS(Hapacol, price.min, strong.min, sideEffect.min);
    Sminus[1] = this.utilS(Aspirin, price.min, strong.min, sideEffect.min);
    Sminus[2] = this.utilS(Paracetamol, price.min, strong.min, sideEffect.min);
    Sminus[3] = this.utilS(Efferagan, price.min, strong.min, sideEffect.min);
    // Caculate C
    C[0] = this.utilC(Splus[0], Sminus[0]);
    C[1] = this.utilC(Splus[1], Sminus[1]);
    C[2] = this.utilC(Splus[2], Sminus[2]);
    C[3] = this.utilC(Splus[3], Sminus[3]);

    return C;
  }

  utilS({price, strong, sideEffect}, priceS, strongS, sideEffectS){
    return Math.sqrt(Math.pow(price-priceS, 2)+Math.pow(strong-strongS, 2)+Math.pow(sideEffect-sideEffectS, 2));
  }

  utilC(max, min){
    return min /(min+max);
  }

  render() {
    return (
      <div className="app">
        <div className="title">
          <h1 id="find-a-docter"><Hospital size={32}/> FIND A DOCTER !</h1>
          <h3 id="supply-medicine">SUPPLY MEDICINE SERVICE FOR EVERYONE AND TOTALY FREE</h3>
          <div className="frame-wrap">
            <InformationFrame nameOfClass={this.state.informationFrame} click={this.click}
              changeFinanceability={this.changeFinanceability}
              changeTemperature={this.changeTemperature}
              changeAge={this.changeAge}
            />
            <ResultFrame nameOfClass={this.state.resultFrame} click={this.click} C_cooked={this.state.C_cooked}/>
          </div>  
        </div>  
      </div>
    );
  }
}

export default App;



